@import "bootstrap/dist/css/bootstrap.css";

.search-bar{
    margin: 20px;
    text-align: center;
}

.search-bar input{
    max-width: 100%;
}

.video-item img{
    max-width: 64px;
}

.video-detail .details{
    margin-top: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.list-group-item{
    cursor: pointer;
}

.list-group-item:hover{
    background-color: #eee;
}